"use client"

import { useState } from "react"
import { useGame } from "@/components/game-state"
import { RPGWindow, RPGButton, RPGBar } from "@/components/rpg-window"

interface Enemy {
  name: string
  hp: number
  maxHp: number
  attack: number
  defense: number
}

const ENEMIES: Enemy[] = [
  { name: "„Çπ„É©„Ç§„É†", hp: 15, maxHp: 15, attack: 3, defense: 1 },
  { name: "„Ç™„Éº„ÇØ", hp: 25, maxHp: 25, attack: 5, defense: 2 },
  { name: "„Éâ„É©„Ç¥„É≥", hp: 50, maxHp: 50, attack: 8, defense: 4 },
]

export function BattlePage() {
  const { gameState, setPage, setMessage, gainExp, takeDamage } = useGame()
  const [enemy, setEnemy] = useState<Enemy>(ENEMIES[0])
  const [battleLog, setBattleLog] = useState<string[]>([`${enemy.name}„Åå „ÅÇ„Çâ„Çè„Çå„ÅüÔºÅ`])
  const [battleActive, setBattleActive] = useState(true)

  const handleAttack = () => {
    const damage = Math.floor(Math.random() * 12) + 5
    const newHp = Math.max(0, enemy.hp - damage)

    setBattleLog((prev) => [...prev, `„ÇÜ„ÅÜ„Åó„ÇÉ„ÅÆ „Åì„ÅÜ„Åí„ÅçÔºÅ ${damage} „ÅÆ „ÉÄ„É°„Éº„Ç∏ÔºÅ`])

    // ÂÄí„Åó„ÅüÂ†¥Âêà
    if (newHp <= 0) {
      const exp = Math.floor(enemy.maxHp * 1.5)
      gainExp(exp)

      setBattleLog((prev) => [
        ...prev,
        `${enemy.name}„Çí „Åü„Åä„Åó„ÅüÔºÅ`,
        `${exp} „ÅÆ„Åë„ÅÑ„Åë„Çì„Å° „Çí „Åà„ÅüÔºÅ`
      ])

      setMessage(`${enemy.name}„Çí „Åü„Åä„Åó ${exp} „ÅÆ„Åë„ÅÑ„Åë„Çì„Å° „Çí „Åà„ÅüÔºÅ`)
      setBattleActive(false)
      return
    }

    // „Åæ„Å†Êïµ„ÅåÁîü„Åç„Å¶„Çã
    setEnemy((prev) => ({ ...prev, hp: newHp }))

    // ‚ñº ÂÆâÂÖ®„Å™ÂèçÊíÉÂá¶ÁêÜ
    setTimeout(() => {
      const enemyDamage = Math.floor(Math.random() * 8) + 2

      // takeDamage „ÇíÂÖà„Å´Âëº„Å∂
      takeDamage(enemyDamage)

      const afterHp = gameState.character.hp - enemyDamage

      setBattleLog((prev) => [
        ...prev,
        `${enemy.name} „ÅÆ „Åì„ÅÜ„Åí„ÅçÔºÅ ${enemyDamage} „ÅÆ „ÉÄ„É°„Éº„Ç∏ÔºÅ`,
      ])

      // HP„Åå0‰ª•‰∏ã„Å´„Å™„Å£„Åü„Çâ„Ç≤„Éº„É†„Ç™„Éº„Éê„Éº
      if (afterHp <= 0) {
        setMessage("„ÇÜ„ÅÜ„Åó„ÇÉ„ÅØ „Å°„Åã„Çâ„Å§„Åç„Åü‚Ä¶")
        setBattleActive(false)
      }
    }, 500)
  }

  const handleFlee = () => {
    setBattleLog((prev) => [...prev, "„ÇÜ„ÅÜ„Åó„ÇÉ„ÅØ „Å´„Åí„Å†„Åó„ÅüÔºÅ"])
    setMessage("„Åü„Åü„Åã„ÅÑ„Åã„Çâ „Å´„Åí„Å†„Åó„Åü„ÄÇ")
    setBattleActive(false)

    setTimeout(() => setPage("home"), 1500)
  }

  return (
    <div className="max-w-2xl mx-auto space-y-4 p-4 bg-[#1a1b29] min-h-screen text-cyan-100">

      {/* Enemy display */}
      <div className="text-center mt-4 enemy-appear">
        <p
          className="text-xl text-cyan-300"
          style={{
            fontFamily: '"Courier New", monospace',
            letterSpacing: "0.08em",
            fontWeight: "bold",
          }}
        >
          ‚ñ† {enemy.name} ‚ñ†
        </p>
      </div>

      {/* Battle Status */}
      <div className="grid grid-cols-2 gap-4">
        <RPGWindow title="„ÇÜ„ÅÜ„Åó„ÇÉ">
          <RPGBar label="Ôº®Ôº∞" current={gameState.character.hp} max={gameState.character.maxHp} color="cyan" />
        </RPGWindow>
        <RPGWindow title={enemy.name}>
          <RPGBar label="Ôº®Ôº∞" current={enemy.hp} max={enemy.maxHp} color="pink" />
        </RPGWindow>
      </div>

      {/* Battle Log */}
      <RPGWindow title="„Åü„Åü„Åã„ÅÑ„ÅÆ„Åç„Çç„Åè">
        <div className="h-40 overflow-y-auto space-y-1 px-1">
          {battleLog.map((log, i) => (
            <p
              key={i}
              className="text-xs text-cyan-400"
              style={{
                fontFamily: '"Courier New", monospace',
                letterSpacing: "0.05em",
                fontWeight: "bold",
              }}
            >
              ‚ñ∂ {log}
            </p>
          ))}
        </div>
      </RPGWindow>

      {/* Commands */}
      {battleActive ? (
        <RPGWindow title="„Ç≥„Éû„É≥„Éâ">
          <div className="space-y-2">
            <RPGButton onClick={handleAttack} className="w-full text-left rpg-menu-item">
              ‚ñ∂ „Åì„ÅÜ„Åí„Åç
            </RPGButton>

            <RPGButton onClick={handleFlee} className="w-full text-left rpg-menu-item">
              ‚ñ∂ „Å´„Åí„Çã
            </RPGButton>
          </div>
        </RPGWindow>
      ) : (
        <RPGButton
          onClick={() => setPage("home")}
          className="w-full mt-4 rpg-menu-item"
        >
          ‚ñ∂ „Éõ„Éº„É†„Å´ „ÇÇ„Å©„Çã
        </RPGButton>
      )}

      <style jsx>{`
        .enemy-appear {
          animation: fadeIn 1.2s ease-out;
        }

        @keyframes fadeIn {
          0% { opacity: 0; transform: translateY(-5px); }
          100% { opacity: 1; transform: translateY(0); }
        }

        .rpg-menu-item:hover {
          filter: brightness(1.3);
          transform: translateX(6px);
          transition: 0.15s;
        }
      `}</style>
    </div>
  )
}
