export function LogoutButton() {
  const router = useRouter();

  const logout = async () => {
    await supabase.auth.signOut();
    router.push("/"); // ‚Üê „Éà„ÉÉ„Éó„Å∏Êàª„Çã
  };

  return <RPGButton className="w-full text-left rpg-menu-item" onClick={logout}>‚ñ∂ „É≠„Ç∞„Ç¢„Ç¶„Éà</RPGButton>;
}


