"use client";

import { useEffect, useState } from "react";
import { supabase } from "@/lib/supabase";
import { RPGWindow, RPGButton } from "@/components/rpg-window";



//ÂûãÂÆöÁæ©
type Skill = {
	id: string;
	name: string;
	description?: string;
};

type UserSkill = {
	id?: string;
	user_id: string;
	skill_id: string;
	level: number;
	name: string;
	description?: string;
};
export default function SkillBoardPage() {
  const [skills, setSkills] = useState<Skill[]>([]);
  const [userSkills, setUserSkills] = useState<UserSkill[]>([]);

  useEffect(() => {
	loadSkills();
  }, []);

  const loadSkills = async () => {
	const { data: skills } = await supabase
	  .from("skill")
	  .select("*");
	const { data: userSkills } = await supabase
	  .from("user_skills")
	  .select("*");

setSkills(skills ?? []);
setUserSkills(userSkills ?? []);
  };

const unlock = async (skillId: string) => {
	await supabase.from("user_skills").insert({
		skill_id: skillId,
		level: 1,
	});
	loadSkills();
}



	return (
		<div className="max-w-lg mx-auto mt-10">
			<RPGWindow title="„Çπ„Ç≠„É´„Éú„Éº„Éâ">
				<div className="grid grid-cols-4 gap-4 p-4">
					{skills.map((s) => {
						const owned = userSkills.find((u) => u.skill_id === s.id);
						const level = owned?.level ?? 0;
						const locked = level === 0;

						return (
							<div
								key={s.id}
								className={`w-16 h-16 border p-2 flex items-center justify-center rounded
				${locked ? "bg-gray-700 text-gray-500" : "bg-yellow-500 text-black"}
				`}
							>
								{locked ? (
									<button onClick={() => unlock(s.id)}>üîí</button>
								) : (
									<span>‚òÖ{level}</span>
								)}
							</div>
						);
					})}
				</div>
			</RPGWindow>
		</div>
	);
}

