// ‚óã√ó„ÇØ„Ç§„Ç∫Áî®„ÅÆÂïèÈ°å„Éá„Éº„ÇøÔºà‰æãÔºâ
const QUESTIONS = [
  {
    id: "1",
    statement: "JavaScript„ÅÆ let „ÅØ block scope „Åß„ÅÇ„Çã„ÄÇ",
    correct: true,
    exp: 20,
    explanation: "let „ÅØ {} „ÅÆ„Éñ„É≠„ÉÉ„ÇØÂÜÖ„ÅßÊúâÂäπ„Å™„Çπ„Ç≥„Éº„Éó„ÇíÊåÅ„Å°„Åæ„Åô„ÄÇ",
  },
  {
    id: "2",
    statement: "React Hooks „ÅØ„ÇØ„É©„Çπ„Ç≥„É≥„Éù„Éº„Éç„É≥„ÉàÂ∞ÇÁî®„ÅÆÊ©üËÉΩ„Åß„ÅÇ„Çã„ÄÇ",
    correct: false,
    exp: 25,
    explanation: "Hooks „ÅØÈñ¢Êï∞„Ç≥„É≥„Éù„Éº„Éç„É≥„Éà„ÅßÁä∂ÊÖã„ÇÑÂâØ‰ΩúÁî®„ÇíÊâ±„ÅÜ„Åü„ÇÅ„ÅÆ‰ªïÁµÑ„Åø„Åß„Åô„ÄÇ",
  },
  {
    id: "3",
    statement: "tailwind CSS „ÅØ utility-first „ÅÆ CSS „Éï„É¨„Éº„É†„ÉØ„Éº„ÇØ„Åß„ÅÇ„Çã„ÄÇ",
    correct: true,
    exp: 20,
    explanation: "Tailwind „ÅØ„É¶„Éº„ÉÜ„Ç£„É™„ÉÜ„Ç£„ÇØ„É©„Çπ„Çí‰∏≠ÂøÉ„Å´ÊßãÊàê„Åï„Çå„Çã„Éï„É¨„Éº„É†„ÉØ„Éº„ÇØ„Åß„Åô„ÄÇ",
  },
]

export function LearnPage() {
  const { setPage, setMessage, gainExp, learnSkill } = useGame()

  // question index
  const [index, setIndex] = useState(0)
  const current = QUESTIONS[index]

  // ÂõûÁ≠îÊ∏à„Åø„Éï„É©„Ç∞„Å®Âà§ÂÆöÁµêÊûú
  const [answered, setAnswered] = useState(false)
  const [isCorrect, setIsCorrect] = useState<boolean | null>(null)

  // ÂõûÁ≠î„Éè„É≥„Éâ„É©
  const handleAnswer = (userAnswer: boolean) => {
    if (answered) return // ‰∫åÈáçÂõûÁ≠îÈò≤Ê≠¢

    const correct = userAnswer === current.correct
    setAnswered(true)
    setIsCorrect(correct)

    if (correct) {
      // Ê≠£Ëß£ÊôÇ„ÅÆÂá¶ÁêÜ
      gainExp(current.exp)
      setMessage(`Ê≠£Ëß£ÔºÅ${current.exp} „ÅÆÁµåÈ®ìÂÄ§„ÇíÁç≤Âæó„Åó„ÅüÔºÅ`)
      learnSkill({
        id: `skill-${current.id}`,
        name: `${current.statement.substring(0, 12)}...`,
        description: current.explanation,
        mpCost: 3,
      })
    } else {
      // ‰∏çÊ≠£Ëß£ÊôÇ„ÅÆ„Éï„Ç£„Éº„Éâ„Éê„ÉÉ„ÇØ
      setMessage("‰∏çÊ≠£Ëß£„Åß„Åô„ÄÇËß£Ë™¨„ÇíÁ¢∫Ë™ç„Åó„Å¶Ê¨°„Å´ÈÄ≤„Åø„Åæ„Åó„Çá„ÅÜ„ÄÇ")
    }
  }

  // Ê¨°„Å∏„Éú„Çø„É≥„Éè„É≥„Éâ„É©Ôºà„É¶„Éº„Ç∂„ÉºÊìç‰Ωú„ÅßÈÄ≤„ÇÄÔºâ
  const handleNext = () => {
    // Êú™ÂõûÁ≠î„Åß Next „ÅåÊäº„Åï„Çå„Åü„Çâ‰Ωï„ÇÇ„Åó„Å™„ÅÑ
    if (!answered) return

    const next = index + 1
    if (next >= QUESTIONS.length) {
      // ÂÖ®ÂïèÁµÇ‰∫Ü
      setMessage("ÂÖ®„Å¶„ÅÆÂïèÈ°å„ÅåÁµÇ‰∫Ü„Åó„Åæ„Åó„Åü„ÄÇ„ÅäÁñ≤„Çå„Åï„ÅæÔºÅ")
      setTimeout(() => setPage("home"), 800) // Â∞ë„Åó„Å†„Åë‰ΩôÈüª„ÇíÊÆã„Åô
      return
    }

    // Ê¨°„ÅÆÂïèÈ°å„Å∏
    setIndex(next)
    setAnswered(false)
    setIsCorrect(null)
  }

  // „É¶„Éº„Ç∂„Éº„ÅåÈÄî‰∏≠„ÅßÂ≠¶Áøí„Çí„ÇÑ„ÇÅ„ÇãÔºà„Éõ„Éº„É†„Å∏Ôºâ
  const handleAbort = () => {
    setPage("home")
    setMessage("Â≠¶Áøí„Çí‰∏≠Êñ≠„Åó„Åæ„Åó„Åü„ÄÇ")
  }

  return (
    <div className="max-w-2xl mx-auto space-y-4">
      <RPGWindow title={`‚óã√ó„ÇØ„Ç§„Ç∫ (${index + 1}/${QUESTIONS.length})`} className="mb-4">
        <p
          style={{ fontFamily: '"Courier New", monospace', letterSpacing: "0.03em", fontWeight: 700 }}
          className="text-sm text-cyan-400 mb-3"
        >
          ÂïèÈ°å„Å´Á≠î„Åà„Å¶ÁµåÈ®ìÂÄ§„ÇíÁç≤Âæó„Åó„Çà„ÅÜ„ÄÇÊ≠£Ë™§„ÅØ„Åô„Åê„Å´Âà§ÂÆö„Åï„Çå„Åæ„Åô„ÄÇ
        </p>

        <div className="mb-4">
          <p
            style={{ fontFamily: '"Courier New", monospace', letterSpacing: "0.02em", fontWeight: 700 }}
            className="text-sm text-yellow-300 mb-2"
          >
            Q: {current.statement}
          </p>

          {/* Âà§ÂÆöÊ∏à„Åø„Å™„ÇâËß£Ë™¨„ÇíÂÖà„Å´Ë°®Á§∫ÔºàË¶ñË™çÊÄßÂêë‰∏äÔºâ */}
          {answered && (
            <div className="mb-3">
              <div className={`p-3 rounded ${isCorrect ? "bg-green-900/50" : "bg-red-900/40"}`}>
                <div className="text-sm font-bold">
                  {isCorrect ? "Ê≠£Ëß£ÔºÅ" : "‰∏çÊ≠£Ëß£"}
                </div>
                <div className="text-xs mt-1 text-gray-200">{current.explanation}</div>
              </div>
            </div>
          )}

          {/* ÂõûÁ≠î„Éú„Çø„É≥Áæ§ÔºàÂõûÁ≠îÊ∏à„Åø„Å™„Çâ disabledÔºâ */}
          <div className="grid grid-cols-2 gap-3">
            <RPGButton
              onClick={() => handleAnswer(true)}
              className="w-full rpg-menu-item"
              // @ts-ignore - className prop pass-through assumed
              disabled={answered}
            >
              ‚óã „Åù„ÅÜ„Å†
            </RPGButton>

            <RPGButton
              onClick={() => handleAnswer(false)}
              className="w-full rpg-menu-item"
              // @ts-ignore
              disabled={answered}
            >
              ‚úó „Å°„Åå„ÅÜ
            </RPGButton>
          </div>
        </div>

        {/* ÂõûÁ≠îÊ∏à„Åø„Å™„ÇâÊ¨°„Å∏„Éú„Çø„É≥„ÇíË°®Á§∫ */}
        <div className="flex gap-2">
          <RPGButton onClick={handleNext} className="flex-1 rpg-menu-item" disabled={!answered}>
            {index + 1 >= QUESTIONS.length ? "ÁµÇ‰∫Ü„Åó„Å¶„Éõ„Éº„É†„Å∏" : "Ê¨°„ÅÆÂïèÈ°å„Å∏"}
          </RPGButton>

          <RPGButton onClick={handleAbort} className="flex-1 rpg-menu-item" disabled={false}>
            ‰∏≠Êñ≠„Åó„Å¶Êàª„Çã
          </RPGButton>
        </div>
      </RPGWindow>
    
    <style jsx>{`
    /* „É°„Éã„É•„Éº„ÅÆ„Éõ„Éê„ÉºÂäπÊûú */
      .rpg-menu-item:hover {
        filter: brightness(1.3);
        transform: translateX(6px);
        transition: 0.15s;
      }
    `}</style>

    </div>
  )
}
